import React, { useState } from 'react'
import { nanoid } from 'nanoid'
import styles from './App.module.css'
import Header from './components/Header'
import Content from './components/Content'
import Footer from './components/Footer'
import json from './components/json/product.json'
import watch from './components/img/watch.jpg'
import styled from './components/Content.module.css'

function App() {
	// const [product, setProduct] = useState([])
	const [hendle, setHendle] = useState(1)
	const [basket, setBasket] = useState([])
	const [count, setCount] = useState(0)
	const [prices, setPrice] = useState(0)

	const liActive = index => {
		setHendle(index)
	}

	const basketListAdd = id => {
		setCount(count + 1)
		let sum = 0
		return json.map(
			(el, index) =>
				index === id &&
				setBasket(
					[
						...basket,
						{
							name: el.name,
							price: el.price,
						},
					],
					setPrice(prices + +el.price)
				)
		)
	}

	const basketDell = index => {
		setCount(count - 1)
		return basket.filter(id => {
			if (id !== index) {
				setBasket([...basket.slice(0, index), ...basket.slice(index + 1)])
					setPrice(prices - +id.price)

			}
		})
	}
	const bsketSum = () => {
		return basket.map(item => setPrice(prices + +item.price))
	}

	console.log(prices)
	// console.log(prices)

	const basketInit = basket.map((item, index) => {
		return (
			<li key={index} id={index} className={styles.basketItem}>
				<img className={styles.basketImg} src={watch} alt='' />
				<div className={styles.basketContent}>
					<h1 className={styles.basketItemTitle}>{item.name}</h1>
					<p className={styles.basketItemPrice}>{item.price}</p>
				</div>
				<button className={styles.bsketBtn} onClick={() => basketDell(index)}>
					<svg
						xmlns='http://www.w3.org/2000/svg'
						width='20'
						height='20'
						fill='inherit'
						className='bi bi-trash style'
						viewBox='0 0 16 16'
					>
						<path
							d='M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5m2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5m3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0z'
							fill='#fff'
						/>
						<path
							d='M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4zM2.5 3h11V2h-11z'
							fill='#fff'
						/>
					</svg>
				</button>
			</li>
		)
	})
	console.log(basket)

	const result = json.map((item, index) => {
		return (
			<li key={id()} id={index - 1} className={styled.listItem}>
				<img className={styled.listItemImg} src={watch} alt='' />
				<h1 className={styled.itemTitle}>{item.name}</h1>
				<p className={styled.itemPrice}>{item.price}</p>
				<button
					className={styled.itemButton}
					onClick={() => basketListAdd(index)}
				>
					в корзину
				</button>
			</li>
		)
	})

	return (
		<div className={styles.wrapper}>
			<Header
				liActive={liActive}
				hendle={hendle}
				basketInit={basketInit}
				count={count}
				prices={prices}
			/>
			<Content id={id} result={result} json={json} />
			<Footer />
		</div>
	)
}

export default App

function id() {
	return nanoid()
}
